/*}*/

.dual_mom_buttons{
    width: 100%;
}

.dual_mom_buttons label{
    padding-right: 4px;
    padding-left: 4px;
}

.button_remove{
    float: right;
}

.ticker_list_heading{
    width: 100%;
    border-bottom-right-radius: 0 !important;
    border-bottom-left-radius: 0 !important;
}

