/*}*/



/*General Interface */

.tax_rate_selector{
    display: inline-flex;
    width: 100%;
}

.tax_type_name {
    width: 13.5rem;
}

.default_col{

}


.width-95{
    width: 95%;
}





/*#chart_container{*/
/*    margin-left: -50px;*/
/*}*/

.row[min-width~="1200px"] #chart_container {
    margin-left: -50px;
}

.grid line {
    stroke: #ddd;
}

.grid .domain{
    stroke: #ddd;
}

#chart_container:hover{
    cursor: crosshair;
}

.chart_tooltip{
    opacity: 0.9;
    background: white;
    box-shadow: 0 0 8px 0 rgba(70,78,86,.25);
    border-radius: 0.4rem;

    padding-right: 0.2rem;

    position: absolute;
    top: 98px;
    left: 80px;

    font-family: "Roboto Mono", Courier, monospace;
}

#ticker_boxes{
    position: absolute;
    top: 65px;
    left: 80px;

    font-family: "Roboto Mono", Courier, monospace;
}

.ticker_box{
    display: inline-block;
    padding: 0.4rem;
    background: white;
    opacity: 0.9;
    border-radius: 0.4rem;
    box-shadow: 0 0 8px 0 rgba(70,78,86,.25);
}

.ticker_box div{
    display: inline-block;
}

.ticker_box_value_div{
    padding-left: 0.5rem;
    width: 4rem;
}

#strategy_box{
    border-left: 1rem solid #1f77b4;
}

#benchmark_box{
    border-left: 1rem solid #ff7f0e;
    margin-left: 1rem;
}


.chart_tooltip td{
    padding: 0.1rem 0.1rem 0.1rem 0.3rem;
    border-top: none;
}

.returns_col{
    text-align: right;
    width: 6rem;
}

.return_line{
    fill: none;
    stroke-width: 0.2rem;
}
.benchmark_line{
    fill: none;
    stroke-width: 0.1rem;
}

.nav-link{
    border-color: #e9ecef #e9ecef #dee2e6 !important;
    cursor: pointer;
}

.nav-link:hover{
    background: #e4e4e4;
}

.table-wrapper-scroll-y{
    max-height: 300px !important;
}

/*Many of the options below overwrite defaults from Mui Autocomplete*/
/*hence the excessive use of !important */
/*I guess the alternative would be to overwrite them in the source*/
/*but then, does that get overwritten with new versions?*/

.ticker_span{
    flex: 0 0 4rem !important;
}

.ticker_selector_root{
    flex: 1 1 7rem !important;
    padding: 0 !important;
    border-bottom: 1px solid #ced4da;
    border-right: 1px solid #ced4da;
    border-left: 1px solid #ced4da;
    /*border-radius: .2rem;*/
}

/*Rounded corners and bottom margin around last ticker*/
.ticker_selector_root:last-child{
    border-bottom-left-radius: .2rem;
    border-bottom-right-radius: .2rem;
    margin-bottom: .25rem;
}

.MuiFormControl-root{
    width: 100%;
}

.ticker_selector_inputroot{
    padding: 0 !important;
    border-bottom: 0 !important;
    background-color: white !important;
}

.ticker_selector_inputroot:before, .ticker_selector_inputroot:after{
    border-bottom: 0 !important;
}

.ticker_selector_input{
    /*width: 100% !important;*/
    padding: .25rem .5rem !important;
    font-size: .875rem !important;
    font-weight: 400;
    font-family: Arial, sans-serif !important;
    color: #495057 !important;
    line-height: 1.5;
}

.ticker_selector_popper{
    width: 400px !important;
}

.MuiIconButton-root{
    background-color: white !important;
}

.MuiIconButton-root:hover{
    background-color: white !important;
    color: black;
}

/*Move clear button all the way to the right*/
.MuiAutocomplete-inputRoot[class*="MuiFilledInput-root"] .MuiAutocomplete-endAdornment{
    right: 2px !important;
    top: -2px;
}

.MuiAutocomplete-clearIndicator{
    /*visibility: visible !important;*/
    margin-top: 3px !important;
    padding: 1px !important;
}

.input_duration{
    flex: 1 1 9rem !important;
}

html {
    font-size: 75%;
}

.display-2{
    text-align: center;
}

.config_row{
    margin-left: 0px;
    background: #f3f3f3;
    border-radius: 1rem;
}

.inner_config_row{
    margin-left: 0;
}

.config_header{
    background: #2469b34d;
    -ms-writing-mode: tb-rl;
        writing-mode: vertical-rl;
    -webkit-text-orientation: mixed;
            text-orientation: mixed;
    font-size: 1.5rem;
    font-weight: 600;
    margin-right: 1rem;

    padding-top: 2rem;
    padding-left: 0.2rem;
    padding-right: 0.2rem;
    border-top-left-radius: 1rem;
    border-bottom-left-radius: 1rem;
}


#chart_container svg{
    /*margin: 10px;*/
    margin-top: 30px;
    margin-bottom: 30px;
    margin-left: 35px;
    margin-right: 30px;
}

